}

.button a {
  border: 0.1rem solid var(--purple);
  width: 5.981875rem;
  height: 1.83125rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--purple) !important;
}

.button a:hover {
  background-color: var(--purple);
  color: var(--white) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.buttonb a {
  font-weight: 500;
  text-transform: uppercase;
  width: 5.981875rem;
  height: 1.83125rem;
  border: 0.1rem solid var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white) !important;
}

.buttonb a:hover {
  background-color: var(--white);
  color: var(--purple) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.buttonc a {
  font-weight: 500;
  text-transform: uppercase;
  border: 0.1rem solid var(--white);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--white) !important;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.buttonc a:hover {
  background-color: var(--white);
  color: var(--purple) !important;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.sniplogo {
  margin: 0;
  width: 4rem;
  height: 4rem;
}

.smlogo {
  fill: var(--purple);
}

nav {
