}
.navmenu :hover {
  color: var(--purple);
  transition: 0.3s;
}

.navbar {
  display: flex;
  align-items: center;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.menu {
  list-style: none;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  gap: 0.5rem;
