* {
  padding: 0;
  margin: 0;
}

body {
  font-size: 1rem;
  box-sizing: border-box;
}

h1 {
  font-size: 3rem;
  font-weight: 400;
  font-family: "DM Serif Display";
  line-height: 3.4rem;
  text-wrap: wrap;
  width: 22rem;
  padding-bottom: 1rem;
  padding-top: 2rem;
}

h1::before {
  content: "";
  display: block;
  width: 6.5rem;
  background-color: transparent;
  border-top: 0.02rem solid var(--white);
  padding-top: 1.4rem;
}

h2 {
  color: var(--black);
  font-size: 3rem;
  font-weight: 100;
  font-family: "DM Serif Display";
  line-height: 3.4rem;
  width: 22rem;
  padding-bottom: 2rem;
}

h2::before {
  content: "";
  display: block;
  padding-top: 2rem;
  width: 6.5rem;
  background-color: transparent;
  border-top: 0.02rem solid var(--grayish-purple);
}

h3 {
  color: var(--white);
  font-size: 2.5rem;
  font-weight: 400;
  font-family: "DM Serif Display";
  line-height: 2.5rem;
}

h4 {
  padding-top: 2rem;
  padding-bottom: 0.8 rem;
  color: var(--black);
  font-size: 1.4rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

.random {
  background-color: var(--white);
  padding-top: 3rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.random a {
  text-decoration: none;
  color: black;
}
.random a:hover {
  text-decoration: underline;
  color: var(--dark-purple);
  transition: 0.8s ease-in-out;
  cursor: pointer;
}

@media screen and (width < 63.9rem) {
  .navbar, .section-two, .section-three, .section-four, .random {
    padding-left: 3rem;
    padding-right: 3rem;
  }
}
@media screen and (width > 23.4375rem) {
  .mobile {
    display: none;
  }
  .desktop {
    display: inline;
  }
}
@media screen and (width <= 23.4375rem) {
  .mobile {
    display: inline;
  }
  .desktop {
    display: none;
  }
}